	module.exports = {"data":{"markdownRemark":{"html":"<p>一位朋友问我，有没有孩子，我说没有孩子。又问，对家庭如何看法?我说也是想有一个好的家庭的，任何一个忙碌的男人，都希望有一个温暖的家，忙了一天回到家里，感到很幸福。但是有的时候没有办法，可望而不可欲。</p>\n<p>在散步的时候，在树上摘了一些桑果吃，想到了卢梭说的自然状态。这样的生活是可望而不可求。得诗一首：</p>\n<p>与大哥同游</p>\n<p>清池弯道傍波澜，</p>\n<p>踏露青石上绿山。</p>\n<p>四柱巍峨镇地脉，</p>\n<p>一塔朦胧没水潭。</p>\n<p>颐和园深寻幽易，</p>\n<p>昆明湖浅见鱼难。</p>\n<p>收尽千枝花艳丽，</p>\n<p>东来霞色照君还。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0096.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0096.md","frontmatter":{"title":"5月25日 星期三"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0096.md absPath of file >>> MarkdownRemark"}}

